import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { URLz } from 'src/app/enums/url.enum';
import { environment } from 'src/environments/environment';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialogConfig } from '@angular/material/dialog';
import { DepositDetailDialogComponent } from 'src/app/shared/components/dialogs/deposit-detail-dialog/deposit-detail-dialog.component';
import { BaseListClass } from 'src/app/class/base-list-class';
@Component({
  selector: 'di-component-name-here',
  templateUrl: './deposit-detail.component.html',
  styleUrls: ['./deposit-detail.component.css']
})
export class DepositDetailComponent extends BaseListClass implements OnInit {

  public _bank : string;
  public _deposit_slip : string;
  public dataSource = new MatTableDataSource();
  public detail_data ;
  public displayedColumns = ['S No','actions','User Name','Mobile No'];

  constructor(
    public injector: Injector,
    public activeRoute: ActivatedRoute,
  ) {
    super(injector);
  }

  ngOnInit(): void {
    this._component = "Deposit Bank Slip Wise Report"
    this._bank = this.activeRoute.snapshot.paramMap.get('bank');
    this._deposit_slip = this.activeRoute.snapshot.paramMap.get('slipId');
    this.getSystems(this._bank,this._deposit_slip)
  }

  getSystems(bank,depSlip){
    this._http.get({
      url: environment.TRANSACTION,
      endpoint: URLz.DEPOSIT_SLIP_DETAIL,
      query: {
        deposit_slip:depSlip,
        bank: bank,
      }
    }).subscribe((res : any)=>{
        this.dataSource.data = res.data.records;
        this.detail_data = res.data.additional;
    })
  }

  openDialog(data) {
    const config: MatDialogConfig = {
      panelClass: "dialog-responsive",
      data:  {source: data}
    }
    const dialogRef = this._dialog.open(
      DepositDetailDialogComponent, config);
    dialogRef.afterClosed().subscribe(result => {return});
  }

}
