import { Component, OnInit ,Injector } from '@angular/core';
import { URLz } from 'src/app/enums/url.enum';
import { MatTableDataSource } from '@angular/material/table';
import { FormArray, FormControl, FormGroup } from '@angular/forms';
import { environment } from 'src/environments/environment';
import Swal from 'sweetalert2';
import { Observable } from 'rxjs';
import { HttpErrorResponse } from '@angular/common/http';
import { BaseForm } from 'src/app/class/base.form';
import { MatDialogConfig } from '@angular/material/dialog';
import { VerificationConfirmationComponent } from 'src/app/shared/components/dialogs/verification-confirmation/verification-confirmation.component';


@Component({
  selector: 'di-component-name-here',
  templateUrl: './e-raseed-deposit-verification.component.html',
  styleUrls: ['./e-raseed-deposit-verification.component.css']
})
export class ERaseedDepositVerificationComponent extends BaseForm implements OnInit {

  public idList = [];
  public receipt_status = '';
  public displayedColumns = ['S No', 'Select', 'Amount', 'Verification no.' ,'Receipt Date' , 'Donation Type','Deposit Slip' ,'Deposit Date'];
  public dataSource = new MatTableDataSource();
  public SEARCH_CRITERIA= [{name:"Username",id:"username"}]
  public componentName : string;
  public cmpname : string = 'verification';
  public donation : number;
  public criteria : string;
  public search_value : string ='';
  public showTable: boolean;

  constructor(
    injector : Injector
  ) {
    super(injector);
    this.param.endpoint = URLz.CASH_DEPOSIT_VERIFY
   }

  ngOnInit(): void {
    this._fhs._pathLocation = '/verification/eraseed_deposit_verification';
    this.componentName = "Cash Deposit Verification";
    this.initForm();
  }

  initForm() {
    this._fs._form = this._fb.group({
      id: this._fb.array([]),
    });
  }

  getStoreFilter(event : any){
    if(event !== '' && event !== undefined && event !== null){
      this.receipt_status = event.status;
      this._http.get({
        url: environment.TRANSACTION,
        endpoint: URLz.CASH_DEPOSIT_VERIFY,
        query: {
          from_date:event.selected.startDate,
          to_date: event.selected.endDate,
          bg:event.bg,
          le:event.le,
          ou:event.ou,
          su:event.su,
          username:event.username,
          bank_account:event.bank,
          depositSlipNumber:event.deposit_slip,
          ftv_no:event.ftv_no,
          receipt_status:event.status,
        }
      }).subscribe({
        next:(res : any)=>{
          this.dataSource.data = res.data.records;
          res?.data?.records.length > 0 ? (this.showTable = true) : (
            this.showTable = false,
            this._swl.noDataFound()
          );
        },
        error:(httpErrorResponse : HttpErrorResponse)=>{
            this._vs._error_server(httpErrorResponse);
            this.showTable = false;
        }
      })
    }
  }

  Switch(bank,slipId){
      if(bank && slipId){
        const url = this._router.serializeUrl(
          this._router.createUrlTree(['/verification/deposit_detail', { bank: bank , slipId: slipId}])
        );
        window.open(url, '_blank');
      }
  }

  SwitchPrint(receipt_type,ver_no){
    let url;
    url = this._router.serializeUrl(
      this._router.createUrlTree(['/blank/acknowledgement_color', {rec_t:receipt_type,ver_no:ver_no, type:'ver'}])
    );
    window.open(url, '_blank');
  }

  getcheckBoxValue(val, masterId , ou , org , sys){
    const selectedIdArr: FormArray = this._fs._form.get('id') as FormArray;
    if (val.target.checked) {
      selectedIdArr.push(
        new FormGroup({
          id: new FormControl(val.target.value),
          master_id: new FormControl(masterId),
          ou:new FormControl(ou),
          organisation_id:new FormControl(org),
          system_id:new FormControl(sys),
        })
      );
    } else {
      let i: number = 0;
      selectedIdArr.controls.forEach((ctrl: FormGroup) => {
        if (
          ctrl.value.id == val.target.value &&
          ctrl.value.master_id == masterId &&
          ctrl.value.ou == ou &&
          ctrl.value.organisation_id == org &&
          ctrl.value.system_id == sys
        ) {
          selectedIdArr.removeAt(i);
          return;
        }
        i++;
      });
    }
  }

  _onSubmit() {
    this._fs._form.markAllAsTouched();
    this._vs._submitted = true;
    if (this._fs._form.valid) {
      Swal.fire({
        title: this._translate.instant('Are you sure?'),
        text: this._translate.instant("You won't be able to revert this!"),
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: this._translate.instant("Yes, Proceed!"),
        cancelButtonText: this._translate.instant("Cancel")
      }).then((result) => {
        if (result.isConfirmed) {
            let modify: Observable<any>;
            modify = this._http.create({body: this._fs._form.value ,url: environment.TRANSACTION ,endpoint: URLz.CASH_DEPOSIT_VERIFY});
            modify.subscribe({
                next:(res: any) => {
                  let heading = 'Processed'
                  let message = res?.message
                  let ver_no = res.data.ver_no;
                  let receipt_type = 1;
                  this.confirmationDialog(heading, message ,ver_no , receipt_type)
                },
                error:(httpErrorResponse: HttpErrorResponse) => {
                    this._vs._error_server(httpErrorResponse.error);
                }
            });
            this._vs._submitted = false;
        }
      })
    } else {
      return this._fs._form.valid;
    }
  }

  confirmationDialog(heading, message ,ver_no , receipt_type) {
    let config: MatDialogConfig = {
      panelClass: "dialog-responsive",
      data: {heading, message ,ver_no,receipt_type}
    }
    const dialogRef = this._dialog.open(VerificationConfirmationComponent, config);
    dialogRef.afterClosed().subscribe((result) => {
      this._fs._form.reset();
      this._fhs.reloadComponent();
    });
  }


}
