import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ERaseedDepositVerificationComponent } from './e-raseed-deposit-verification/e-raseed-deposit-verification.component';
import { DepositDetailComponent } from './deposit-detail/deposit-detail.component';

import { ACTION } from 'src/app/enums/action.enum';
const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: '',
        redirectTo: 'eraseed_deposit_verification',
        pathMatch: 'full'
      },
      {
        path: "eraseed_deposit_verification",
        component: ERaseedDepositVerificationComponent,
        // canDeactivate: [FormGuard],
        data: {
          title: "Bank Deposit Verification",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Bank Deposit Verification"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "deposit_detail",
        component: DepositDetailComponent,
        data: {
          title: "E-raseed Deposit Detail",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "E-raseed Deposit Detail"},
            { title: "Add" },
          ]
        }
      },
      // {
      //   path: "ou",
      //   component: OUListComponent,
      //   data: {
      //     title: "Operating Unit",
      //     urls: [
      //       { title: "Operating Unit" },
      //       { title: "List" },
      //     ],
      //   },
      // },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DepositVerificationRoutingModule { }
